t: flex-start;\n  padding-left: 10px;\n}\n\n.header-content h1 {\n  font-style: italic;\n  margin-left: 29px;\n  font-size: 38px;\n  margin-bottom: 4px;\n}\n\n/* title */\n.title {\n  font-family: 'Great Vibes', cursive;\n  letter-spacing: 1px;\n  transition: transform 0.3s ease;\n}\n\n.title:hover {\n  transform: scale(2.5) translateX(5px) translateY(4px);\n}\n\n/* magnify on hover */\n.magnify-on-hover {\n  transition: transform 0.3s ease;\n}\n\n.magnify-on-hover:hover {\n  transform: scale(1.2);\n}\n\n/* logo */\n#logo {\n  width: 100px;\n  height: 100px;\n  margin: 30px;\n  margin-top: 50px;\n  cursor: pointer;\n  border: 3px solid rgb(11, 166, 52);\n  border-radius: 50%;\n  padding: 10px;\n  display: inline-block;\n  transition: transform 0.3s ease;\n  object-fit: contain;\n}\n\n#logo:hover {\n  transform: scale(1.2);\n  background-color: #C0C0C0;\n}\n\n.sparkle {\n  position: absolute;\n  width: 50px;\n  height: 50px;\n  background: radial-gradient(circle, #c0c0c0 0%, rgba(0, 0, 255, 0.6) 70%, rgba(12, 234, 34, 0) 100%);\n  border-radius: 50%;\n  pointer-events: none;\n  mix-blend-mode: screen;\n  opacity: 0;\n  transition: opacity 0.5s ease-out;\n}\n\n.right-align {\n  display: flex;\n  justify-content: flex-end;\n}\n\n/* login form */\n#login-heading {\n  margin-bottom: 20px;\n  position: relative;\n}\n\n#login-heading:hover {\n  visibility: hidden;\n}\n\n#login-heading::after {\n  content: \"Greetings, please feel free to proceed.\";\n  color: white;\n  font-size: 50px;\n  font-family: 'Great Vibes', cursive;\n  opacity: 0;\n  transition: opacity 0.3s ease;\n  position: absolute;\n  bottom: 20px;\n  left: 10px;\n  visibility: hidden;\n  opacity: 1;\n}\n\n#login-heading:hover::after {\n  opacity: 1;\n  visibility: visible;\n}\n\n#login-form input {\n  display: block;\n  width: 20%;\n  padding: 5px;\n  margin-bottom: 20px;\n}\n\n#login-form label {\n  margin-bottom: 10px;\n  display: block;\n  margin-right: 10px;\n}\n\n#login-form button {\n  padding: 20px 30px;\n  font-size: 19px;\n  cursor: pointer;\n  border: 3px solid rgb(11, 166, 52);\n  border-radius: 60%;\n  display: inline-block;\n  transition: transform 0.3s ease;\n  transform: scale(1);\n  background-color: white;\n  background-image: url('../images/plane.png');\n  background-size: cover;\n  background-repeat: no-repeat;\n  background-position: center;\n  background-size: 50%;\n}\n\n#login-form button:hover {\n  transform: scale(1.2);\n  background-color: white;\n}\n\n#loginError {\n  width: 40%;\n  margin-left: -10%;\n  margin-top: 20px;\n  margin-bottom: 20px;\n  padding: 10px;\n  color: red;\n  font-weight: bold;\n  border: 1px solid red;\n  border-radius: 5px;\n  background-color: rgba(255, 0, 0, 0.1);\n  text-align: center;\n}\n\n#login-section,\n#traveler-dashboard {\n  margin: auto;\n  width: 75%;\n  font-family: 'Raleway', sans-serif;\n  font-size: 24px;\n  padding: 200px;\n}\n\n/* trip section */\n#trip-section,\n#total-spent {\n  margin: 2em;\n  padding: 2em;\n  border: 1px solid rgb(8, 9, 0);\n  border-radius: 10px;\n  transition: background-color 0.3s ease;\n}\n\n#trip-section:hover,\n#total-spent:hover {\n  background-color: rgba(255, 255, 255, 0.2);\n}\n\n/* home page */\n.logo {\n  height: 120px;\n  width: 200px;\n  margin-right: 50px;\n}\n\n.home-page {\n  height: 100%;\n}\n\n/* Widgets */\n.widget::-webkit-scrollbar {\n  width: 10px;\n  height: 10px;\n}\n\n.widget::-webkit-scrollbar-thumb {\n  background-color: #10ea3c;\n  border-radius: 5px;\n}\n\n.widget::-webkit-scrollbar-track {\n  background-color: #eef5ef;\n}\n\n.widget-container {\n  display: grid;\n  grid-template-columns: 1fr 1fr 1fr;\n  gap: 20px;\n  margin-top: 100px;\n}\n\n.widget {\n  font-family: 'Arial', sans-serif;\n  font-size: 16px;\n  font-weight: normal;\n  color: #f6eeee;\n  text-rendering: optimizeLegibility;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n  max-width: 450px;\n  max-height: 600px;\n  margin: 20px auto;\n  padding: 20px;\n  border-radius: 5px;\n  overflow: auto;\n  box-shadow: 0 10px 20px rgba(255, 255, 255, 0.3), 0 6px 6px rgba(255, 255, 255, 0.4);\n}\n\n#dateElement {\n  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);\n  font-family: 'Roboto', sans-serif;\n  font-size: 1em;\n  color: #f4eeee;\n  text-align: center;\n  padding: 10px;\n  margin: 0;\n  background-color: radial-gradient(circle, rgb(5, 98, 45) 0%, rgb(1, 24, 8) 100%);\n  border-bottom: 1px solid #39FF14;\n  position: absolute;\n  top: 80px;\n  left: 80%;\n  transform: translateX(-50%);\n  top: 77%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  z-index: 2;\n}\n\n.header-welcome {\n  font-family: 'Roboto', sans-serif;\n  letter-spacing: 1px;\n  transition: transform 0.3s ease;\n  background-color: rgb(255, 255, 255) 0%, rgb(242, 242, 242) 100%;\n  letter-spacing: 2px;\n  font-size: 2em;\n  width: 20%;\n  padding: 1%;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  position: absolute;\n  top: 75px;\n  left: 73.5%;\n  right: 500px;\n  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);\n}\n\n#omarKhayyam {\n  font-style: italic;\n  font-family: 'Georgia', serif;\n  font-size: 19px;\n  line-height: 1.6;\n  color: #f7f3f3;\n  margin: 20px 0;\n  text-align: center;\n  border-top: 1px solid #10ea3c;\n  border-bottom: 1px solid #10ea3c;\n  padding: 20px 0;\n  position: absolute;\n  top: 10%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n}\n\n.main-section {\n  display: flex;\n  justify-content: space-evenly;\n  height: 85%;\n  align-items: center;\n}\n\n.hidden {\n  display: none;\n}\n\n* {\n  box-sizing: border-box;\n  margin: 0;\n  padding: 0;\n}\n\n#form {\n  max-width: 400px;\n  margin: 20px auto;\n  padding: 20px;\n  background-color: #130101;\n  border-radius: 5px;\n  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);\n  grid-column: 2;\n}\n\n#form h1 {\n  text-align: center;\n  margin-bottom: 20px;\n}\n\n#form label {\n  display: block;\n  margin-bottom: 5px;\n}\n\n#destinationDropdown,\n#calendar,\n#durationInput,\n#numTravelersInput {\n  width: 100%;\n  padding: 10px;\n  margin-bottom: 20px;\n  border: 1px solid #ccc;\n  border-radius: 3px;\n}\n\n#estimatedCost,\n#xyam {\n  text-align: center;\n  margin-bottom: 20px;\n}\n\n#submitTrip {\n  display: block;\n  width: 100%;\n  padding: 10px;\n  background-color: #10ea3c;\n  color: #fff;\n  border: none;\n  border-radius: 3px;\n  cursor: pointer;\n  margin-bottom: 20px;\n  font-family: 'Arial', sans-serif;\n  font-size: 16px;\n  font-weight: normal;\n  color: #040000;\n  text-rendering: optimizeLegibility;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n  position: relative;\n  z-index: 1;\n}\n\n#submitTrip:hover {\n  background-color: #0abdef;\n}\n\n#pastTrips.widget,\n#upcomingTrips.widget {\n  color: rgb(250, 247, 247);\n  text-align: center;\n  margin: auto;\n  font-family: 'Arial', sans-serif;\n  font-size: 16px;\n  font-weight: bold;\n  line-height: 1.5;\n}\n\n.trips-list {\n  list-style-type: none;\n  padding-left: 0;\n}\n\nimg {\n  margin: 10px;\n}\n\n#green {\n  width: 800px;\n  height: 800px;\n  margin: 80px;\n  position: absolute;\n  top: 100px;\n  right: 180px;\n  margin-top: auto;\n  cursor: pointer;\n  border: 3px solid rgb(11, 166, 52);\n  border-radius: 50%;\n  padding: 10px;\n  transition: transform 0.3s ease;\n}\n\n#green:hover {\n  transform: scale(1.2);\n  background-color: #39FF14;\n}\n\n#in-a-hurry {\n  width: 300px;\n  height: 300px;\n  cursor: pointer;\n  border: 3px solid rgb(11, 166, 52);\n  border-radius: 50%;\n  padding: 10px;\n  transition: transform 0.3s ease;\n  object-fit: cover;\n  margin: 10px;\n  position: absolute;\n  top: 50%;\n  right: 31%;\n  transform: translate(50%, 50%);\n  z-index: 3;\n}\n\n#airplane {\n  width: 300px;\n  height: 300px;\n  cursor: pointer;\n  border: 3px solid rgb(11, 166, 52);\n  border-radius: 50%;\n  padding: 10px;\n  transition: transform 0.3s ease;\n  object-fit: cover;\n  margin: 10px;\n  position: absolute;\n  top: 50%;\n  left: 31%;\n  transform: translate(-50%, -50%);\n  z-index: 3;\n}\n\n#in-a-hurry:hover {\n  transform: scale(1.2);\n  background-color: #0abdef;\n}\n\n#airplane:hover {\n  transform: scale(1.2);\n  background-color: #dc0c17;\n}\n\n/* responsive */\n@media screen and (max-width: 768px) {\n\n  .right-align {\n    justify-content: center;\n    flex-wrap: wrap;\n  }\n\n  #airplane,\n  #in-a-hurry,\n  #dateElement {\n    margin: 10px auto;\n  }\n\n  .widget,\n  #form {\n    max-width: 90%;\n  }\n}\n\n@media screen and (max-width: 480px) {\n\n  #form label {\n    font-size: 14px;\n  }\n\n  #destinationDropdown,\n  #calendar,\n  #durationInput,\n  #numTravelersInput {\n    font-size: 14px;\n  }\n\n  #submitTrip {\n    text-align: center;\n    font-size: 16px;\n  }\n}\n\n@media only screen and (max-width: 1024px) {\n\n  #green,\n  #airplane,\n  #in-a-hurry,\n  #dateElement {\n    width: 600px;\n    height: 600px;\n  }\n}\n\n@media only screen and (max-width: 768px) {\n\n  #green,\n  #airplane,\n  #in-a-hurry,\n  #dateElement {\n    width: 400px;\n    height: 400px;\n  }\n\n  #login-section,\n  #traveler-dashboard {\n    width: 90%;\n  }\n}\n\n@media only screen and (max-width: 480px) {\n\n  #green,\n  #airplane,\n  #in-a-hurry,\n  #dateElement {\n    width: 200px;\n    height: 200px;\n  }\n\n  #login-section,\n  #traveler-dashboard {\n    width: 100%;\n  }\n\n  .header-content {\n    flex-direction: column;\n    align-items: center;\n  }\n\n  .header-content h1 {\n    margin-left: 0;\n    margin-top: 10px;\n    font-size: 18px;\n  }\n\n  #logo {\n    margin: 10px auto;\n    width: 30px;\n    height: 30px;\n  }\n}\n"],"sourceRoot":""}]);
